import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {BsModalRef} from 'ngx-bootstrap';
import {AllotmentService} from '../../services/allotment.service';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {NotificationService} from "../../services/notification.service";

@Component({
    selector: 'app-edit-allotment',
    templateUrl: './edit-allotment.component.html'
})
export class EditAllotmentComponent implements OnInit {


    public allotmentForm: FormGroup;
    @Input() allotmentEdit;
    @Output() allotmentOutput = new EventEmitter();

    constructor(public bsModalRef: BsModalRef,
                private allotmentService: AllotmentService,
                private notificationService: NotificationService) {
    }

    ngOnInit() {
        this.allotmentForm = new FormGroup({
            'idAllotment': new FormControl(this.allotmentEdit.idAllotment),
            'number': new FormControl(this.allotmentEdit.number),
            'squaremeter': new FormControl(this.allotmentEdit.squaremeter, [
                Validators.required,
                Validators.min(1),
                Validators.max(500)
            ]),
            'treeNumber': new FormControl(this.allotmentEdit.treeNumber, [
                Validators.required,
                Validators.min(0),
                Validators.max(20)
            ]),
            'composter': new FormControl(this.allotmentEdit.composter),
            'bower': new FormControl(this.allotmentEdit.bower)
        });
    }

    close() {
        this.bsModalRef.hide();
    }

    submit() {
        this.allotmentService.editAllotment(this.allotmentForm.value.idAllotment, this.allotmentForm.value)
            .subscribe(response => {
                    this.bsModalRef.hide();
                    this.notificationService.showNotification('top', 'center', 'success', response);
                    this.allotmentOutput.emit(this.allotmentForm.value);
                },
                err => {
                    console.log(err);
                });
    }


}



