import {Component, Input, OnInit} from '@angular/core';
import {BsModalRef} from 'ngx-bootstrap';
import {UserAllotment} from '../../models/UserAllotment';
import {FormControl, FormGroup, Validators} from '@angular/forms';

@Component({
    selector: 'app-show-info',
    templateUrl: './show-info.component.html'
})
export class ShowInfoComponent implements OnInit {

    @Input() allotmentUser;
    public allotmentList: Array<UserAllotment>;
    public infoGroup: FormGroup;

    constructor(public bsModalRef: BsModalRef) {
    }

    ngOnInit() {
        if (this.allotmentUser.userDto) {
            this.initForm();
        } else {
            this.initAllotmentFormOnly();
        }
    }

    initForm() {
        this.infoGroup = new FormGroup({
            'idAllotment': new FormControl({value: this.allotmentUser.allotmentDto.idAllotment, disabled: true}, Validators.required),
            'number': new FormControl({value: this.allotmentUser.allotmentDto.number, disabled: true}, Validators.required),
            'squaremeter': new FormControl({value: this.allotmentUser.allotmentDto.squaremeter, disabled: true}, Validators.required),
            'treeNumber': new FormControl({value: this.allotmentUser.allotmentDto.treeNumber, disabled: true}, Validators.required),
            'composter': new FormControl({value: this.allotmentUser.allotmentDto.composter, disabled: true}, Validators.required),
            'bower': new FormControl({value: this.allotmentUser.allotmentDto.bower, disabled: true}, Validators.required),
            'idUser': new FormControl({value: this.allotmentUser.userDto.idUser, disabled: true}, Validators.required),
            'email': new FormControl({value: this.allotmentUser.userDto.email, disabled: true}, Validators.required),
            'username': new FormControl({value: this.allotmentUser.userDto.username, disabled: true}, Validators.required),
            'phone': new FormControl({value: this.allotmentUser.userDto.phone, disabled: true}, Validators.required),
            'name': new FormControl({value: this.allotmentUser.userDto.name, disabled: true}, Validators.required),
            'lastName': new FormControl({value: this.allotmentUser.userDto.lastName, disabled: true}, Validators.required),
            'active': new FormControl({value: this.allotmentUser.userDto.active, disabled: true}, Validators.required),
            'roleName': new FormControl({value: this.allotmentUser.userDto.role.substring(5), disabled: true}, Validators.required),
            'allotmentId': new FormControl({value: this.allotmentUser.allotmentDto.idAllotment, disabled: true}, Validators.required)
        });
    }

    initAllotmentFormOnly() {
        this.infoGroup = new FormGroup({
            'idAllotment': new FormControl({value: this.allotmentUser.allotmentDto.idAllotment, disabled: true}, Validators.required),
            'number': new FormControl({value: this.allotmentUser.allotmentDto.number, disabled: true}, Validators.required),
            'squaremeter': new FormControl({value: this.allotmentUser.allotmentDto.squaremeter, disabled: true}, Validators.required),
            'treeNumber': new FormControl({value: this.allotmentUser.allotmentDto.treeNumber, disabled: true}, Validators.required),
            'composter': new FormControl({value: this.allotmentUser.allotmentDto.composter, disabled: true}, Validators.required),
            'bower': new FormControl({value: this.allotmentUser.allotmentDto.bower, disabled: true}, Validators.required),
        });
    }

    close() {
        this.bsModalRef.hide();
    }

}



