import {AfterContentInit, AfterViewInit, Component, OnInit} from '@angular/core';
import {AllotmentService} from '../services/allotment.service';
import {UserService} from '../services/user.service';
import {NotificationService} from 'app/services/notification.service';
import {UserAllotment} from '../models/UserAllotment';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {Allotment} from '../models/Allotment';
import {AuthenticationService} from '../auth/authentication.service';

@Component({
    selector: 'app-user-profile',
    templateUrl: './user-profile.component.html',
    styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent implements OnInit {

    private userEdit;
    public loaded = false;
    public role: string;
    private allotmentEdit: Allotment;
    public userForm: FormGroup;
    public allotmentList: Array<UserAllotment>;

    constructor(private allotmentService: AllotmentService,
                private userService: UserService,
                private authenticationService: AuthenticationService,
                private notificationService: NotificationService) {
    }

    ngOnInit() {
        this.role = this.authenticationService.getRole();
        if (this.role === 'ADMIN') {
            this.getUserActive();
            this.getAllAllotment();
        } else {
            this.getUserPrepareActive();
        }
    }

    prepareForm() {
        this.userForm = new FormGroup({
            'idUser': new FormControl({value: this.userEdit.idUser, disabled: true}, Validators.required),
            'email': new FormControl(this.userEdit.email, [
                Validators.required,
                Validators.email,
            ]),
            'username': new FormControl(this.userEdit.username, Validators.required),
            'phone': new FormControl(this.userEdit.phone, [
                Validators.required,
                Validators.pattern('^\\s*(?:\\+?\\d{1,3})?[- (]*\\d{3}(?:[- )]*\\d{3})?[- ]*\\d{4}(?: *[x/#]\\d+)?\\s*$')
            ]),
            'name': new FormControl(this.userEdit.name, [
                Validators.required,
                Validators.minLength(3),
                Validators.maxLength(20)
            ]),
            'lastName': new FormControl(this.userEdit.lastName, [
                Validators.required,
                Validators.minLength(3),
                Validators.maxLength(30)
            ]),
            'active': new FormControl(this.userEdit.active, [
                Validators.required
            ]),
            'roleName': new FormControl(this.userEdit.role.substring(5), [
                Validators.required
            ]),
            'allotmentId': new FormControl(this.allotmentEdit.idAllotment, [
                Validators.required
            ]),
        });
    }


    getUserActive() {
        this.userService.getActiveUser().subscribe(
            (data: UserAllotment) => {
                this.userEdit = data.userDto;
                if (data.allotmentDto) {
                    this.allotmentEdit = data.allotmentDto
                } else {
                    this.allotmentEdit = new Allotment(0, 0, 0, 0, false, false);
                }
            },
            err => console.error(err)
        );
    }

    getUserPrepareActive() {
        this.userService.getActiveUser().subscribe(
            (data: UserAllotment) => {
                this.userEdit = data.userDto;
                if (data.allotmentDto) {
                    this.allotmentEdit = data.allotmentDto
                } else {
                    this.allotmentEdit = new Allotment(0, 0, 0, 0, false, false);
                }
                this.prepareForm();
                this.userForm.controls['roleName'].disable();
                this.userForm.controls['allotmentId'].disable();
                this.loaded = true;
            },
            err => console.error(err)
        );
    }

    getAllAllotment() {
        this.allotmentService.getAllAllotments().subscribe(
            (data: Array<UserAllotment>) => {
                this.allotmentList = data;
                this.prepareForm();
                this.loaded = true;
                // this.allotmentList = data.map(item => item.allotmentDto);
            },
            err => console.error(err)
        );
    }

    submit() {
        this.userService.editUser(this.userEdit.idUser, this.userForm.getRawValue())
            .subscribe(response => {
                    this.notificationService.showNotification('top', 'center', 'success', response);
                },
                err => {
                    this.notificationService.showNotification('top', 'center', 'danger', err.error);
                    console.log(err);
                });
    }

}



