import {Component, OnInit} from '@angular/core';

import {AuthenticationService} from '../authentication.service';
import {ActivatedRoute, Router} from '@angular/router';
import {JwtAuthenticationRequest} from '../../models/JwtAuthenticationRequest';
import {NotificationService} from '../../services/notification.service';
import {FormControl, FormGroup, Validators} from '@angular/forms';

@Component({
    selector: 'app-signin',
    templateUrl: './signin.component.html',
    styleUrls: ['./signin.component.css']
})
export class SigninComponent implements OnInit {
    loading = false;
    returnUrl: string;
    public loginForm: FormGroup;

    constructor(private route: ActivatedRoute,
                private router: Router,
                private authenticationService: AuthenticationService,
                private notificationService: NotificationService) {
    }

    ngOnInit() {
        // reset login status
        this.authenticationService.logout();

        // get return url from route parameters or default to '/'
        this.returnUrl = this.route.snapshot.queryParams['dashboard'] || '/dashboard';
        this.loginForm = new FormGroup({
            'username': new FormControl('', Validators.required),
            'password': new FormControl('', Validators.required),
        });
    }

    login() {
        this.loading = true;
        this.authenticationService.login(new JwtAuthenticationRequest(this.loginForm.get('username').value, this.loginForm.get('password').value))
            .subscribe(
                data => {
                    this.router.navigate([this.returnUrl]);
                },
                error => {
                    this.notificationService.showNotification('top', 'center', 'danger', error.error);
                    console.log(error);
                    this.loading = false;
                });
    }
}



