import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {BsModalRef} from 'ngx-bootstrap';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {NotificationService} from '../../services/notification.service';
import {ArticleService} from '../../services/article.service';

@Component({
    selector: 'app-add-article',
    templateUrl: './add-article.component.html',
    styleUrls: ['./add-article.component.css']
})
export class AddArticleComponent implements OnInit {

    public articleForm: FormGroup;
    @Input() articleEdit;
    @Output() articleOutput = new EventEmitter();

    constructor(public bsModalRef: BsModalRef,
                private articleService: ArticleService,
                private notificationService: NotificationService) {
    }

    ngOnInit() {
        this.articleForm = new FormGroup({
            'title': new FormControl('', [
                Validators.required,
                Validators.minLength(1),
                Validators.maxLength(255)
            ]),
            'text': new FormControl('', [
                Validators.required,
                Validators.minLength(1),
                Validators.maxLength(25000)
            ])
        });
    }

    close() {
        this.bsModalRef.hide();
    }

    submit() {
        this.articleService.newArticle(this.articleForm.getRawValue())
            .subscribe(response => {
                    this.bsModalRef.hide();
                    this.notificationService.showNotification('top', 'center', 'success', response);
                    this.articleOutput.emit(this.articleForm.value);
                },
                err => {
                    this.bsModalRef.hide();
                    console.log(err);
                    this.notificationService.showNotification('top', 'center', 'danger', JSON.parse(err.error)['message']);
                });
    }
}



