import {Component, OnInit, TemplateRef} from '@angular/core';
import {ArticleService} from '../services/article.service';
import {Article} from '../models/Article';
import {BsModalRef, BsModalService} from 'ngx-bootstrap';
import {NotificationService} from 'app/services/notification.service';
import {EditArticleComponent} from './edit-article/edit-article.component';
import {AuthenticationService} from '../auth/authentication.service';
import {AddArticleComponent} from './add-article/add-article.component';
import {Commentary} from '../models/Commentary';
import {AddCommentaryComponent} from './commentary/add-commentary/add-commentary.component';
import {CommentaryService} from '../services/commentary.service';
import {EditCommentaryComponent} from './commentary/edit-commentary/edit-commentary.component';

@Component({
    selector: 'app-articles',
    templateUrl: './articles.component.html',
    styleUrls: ['./articles.component.css']
})
export class ArticlesComponent implements OnInit {

    bsModalRef: BsModalRef;
    public articles: Array<Article>;
    public username: string;
    public articleId: number;
    public commentaryId: number;
    public show = false;
    public clicked = false;

    constructor(private articleService: ArticleService,
                private notification: NotificationService,
                private modalService: BsModalService,
                private commentaryService: CommentaryService,
                private authentiactionService: AuthenticationService) {
    }

    ngOnInit() {
        this.username = this.authentiactionService.getUsername();
        this.getAllArticles();
    }

    getAllArticles() {
        this.articleService.getAllArticles().subscribe(
            (data: Array<Article>) => {
                this.articles = data;
                this.show = true;
            },
            err => console.error(err)
        );
    }

    addValue(value) {
        const article = new Article();
        article.idArticle = this.articles.length + 1;
        article.username = this.username;
        article.text = value.text;
        article.title = value.title;

        this.articles.push(article);
    }

    addArticle() {
        this.bsModalRef = this.modalService.show(AddArticleComponent, {class: 'modal-lg'});
        this.bsModalRef.content.commentaryOutput.subscribe((value) => {
            this.addValue(value);
        });
    }

    updateValue(value, idArticle) {
        const article = this.articles.find(item => item.idArticle === idArticle);
        article.title = value.title;
        article.text = value.text;
        article.date = new Date();
    }

    editArticle(article) {
        const initialState = {
            articleEdit: article
        };
        this.bsModalRef = this.modalService.show(EditArticleComponent, {initialState, class: 'modal-lg'});
        this.bsModalRef.content.commentaryOutput.subscribe((value) => {
            this.updateValue(value, article.idArticle);
        });
    }

    deleteArticle(template: TemplateRef<any>, articleId) {
        this.articleId = articleId;
        this.bsModalRef = this.modalService.show(template, {class: 'modal-lg'});
    }

    confirm(): void {
        this.articleService.deleteArticle(this.articleId).subscribe(
            (response) => {
                this.notification.showNotification('top', 'center', 'success', response);
                this.deleteValue(this.articleId);
            },
            err => {
                console.log(err);
                this.notification.showNotification('top', 'center', 'danger', JSON.parse(err.error)['message']);
            }
        );
        this.bsModalRef.hide();
    }

    deleteValue(idArticle) {
        this.articles.forEach((item, index) => {
            if (item.idArticle === idArticle) {
                this.articles.splice(index, 1);
            }
        });
    }

    decline(): void {
        this.bsModalRef.hide();
    }

    addCommentaryToList(value, idArticle) {
        const article = this.articles.find(item => item.idArticle === idArticle);
        const commentary = new Commentary();
        commentary.text = value.text;
        commentary.date = new Date();
        commentary.author = this.username;
        commentary.idCommentary = idArticle;
        article.commentaryDtoList.push(commentary);
    }

    showCommentaries(clickedArticle) {
        clickedArticle.showCommentaries ? clickedArticle.showCommentaries = false : clickedArticle.showCommentaries = true;
    }

    addCommentary(article) {
        const initialState = {
            article: article
        };
        this.bsModalRef = this.modalService.show(AddCommentaryComponent, {initialState, class: 'modal-lg'});
        this.bsModalRef.content.commentaryOutput.subscribe((value) => {
            this.addCommentaryToList(value, article.idArticle);
        });
    }

    editCommentary(commentary, article) {
        const initialState = {
            article: article,
            commentaryEdit: commentary
        };
        this.bsModalRef = this.modalService.show(EditCommentaryComponent, {initialState, class: 'modal-lg'});
        this.bsModalRef.content.commentaryOutput.subscribe((value) => {
            this.editCommentaryList(value, article.idArticle);
        });
    }

    editCommentaryList(value, idArticle) {
        const article = this.articles.find(item => item.idArticle === idArticle);
        article.commentaryDtoList.forEach((item) => {
            if (item.idCommentary === value.idCommentary) {
                item.text = value.text;
                item.date = new Date();
            }
        });
    }

    deleteComment(template: TemplateRef<any>, commentaryId, idArticle) {
        this.articleId = idArticle;
        this.commentaryId = commentaryId;
        this.bsModalRef = this.modalService.show(template, {class: 'modal-lg'});
    }

    confirmDeleteComment() {
        this.commentaryService.deleteCommentary(this.commentaryId).subscribe(
            (response) => {
                this.notification.showNotification('top', 'center', 'success', response);
                this.deleteCommentaryValue(this.commentaryId);
            },
            err => {
                console.log(err);
                this.notification.showNotification('top', 'center', 'danger', JSON.parse(err.error)['message']);
            }
        );
        this.bsModalRef.hide();
    }

    declineDeleteComment() {
        this.bsModalRef.hide();
    }

    deleteCommentaryValue(idCommentary) {
        const article = this.articles.find(item => item.idArticle === this.articleId);
        article.commentaryDtoList.forEach((item, index) => {
            if (item.idCommentary === idCommentary) {
                this.articles.splice(index, 1);
            }
        });
    }

}



