import {Component, OnInit} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {UserAllotment} from '../models/UserAllotment';
import {AllotmentService} from '../services/allotment.service';
import {Geojson} from '../models/Geojson';
import {ShowInfoComponent} from './show-info/show-info.component';
import {BsModalRef, BsModalService} from 'ngx-bootstrap';
import {isNullOrUndefined} from 'util';

declare const google: any;
declare var jquery: any;
declare var $: any;

interface Marker {
    lat: number;
    lng: number;
    label?: string;
    draggable?: boolean;
}

@Component({
    selector: 'app-maps',
    templateUrl: './maps.component.html',
    styleUrls: ['./maps.component.css']
})
export class MapsComponent implements OnInit {

    bsModalRef: BsModalRef;
    public loaded = false;
    private data: Geojson;
    public infowindow;
    public map;
    public allotmentList: Array<UserAllotment>;

    constructor(private http: HttpClient,
                private allotmentService: AllotmentService,
                public modalService: BsModalService) {
    }

    ngOnInit() {
        this.loadData();
    }

    loadData() {
        this.http.get('/assets/maps/map.json').subscribe(
            (data: Geojson) => {
                this.data = data;
                this.getAllAllotment();
            },
            err => console.error(err)
        );
    }

    getAllAllotment() {
        this.allotmentService.getAllAllotments().subscribe(
            (data: Array<UserAllotment>) => {
                this.allotmentList = data;
                this.loaded = true;
                this.createMap();
            },
            err => console.error(err)
        );
    }

    mapFeaturesToAllotment() {
        this.data.features.forEach(item => {
            this.allotmentList.forEach(allotment => {
                if (item.properties.id === allotment.allotmentDto.number) {
                    item.properties.info = allotment;
                }
            });
        });
    }

    createMap() {
        this.mapFeaturesToAllotment();
        const myLatlng = new google.maps.LatLng(51.07327, 17.02385);
        const mapOptions = {
            zoom: 17,
            center: myLatlng,
            scrollwheel: false,
            styles: [{
                'featureType': 'water',
                'stylers': [{
                    'saturation': 43
                }, {
                    'lightness': -11
                }, {
                    'hue': '#0088ff'
                }]
            }, {
                'featureType': 'road',
                'elementType': 'geometry.fill',
                'stylers': [{
                    'hue': '#ff0000'
                }, {
                    'saturation': -100
                }, {
                    'lightness': 99
                }]
            }, {
                'featureType': 'road',
                'elementType': 'geometry.stroke',
                'stylers': [{
                    'color': '#808080'
                }, {
                    'lightness': 54
                }]
            }, {
                'featureType': 'landscape.man_made',
                'elementType': 'geometry.fill',
                'stylers': [{
                    'color': '#ece2d9'
                }]
            }, {
                'featureType': 'poi.park',
                'elementType': 'geometry.fill',
                'stylers': [{
                    'color': '#ccdca1'
                }]
            }, {
                'featureType': 'road',
                'elementType': 'labels.text.fill',
                'stylers': [{
                    'color': '#767676'
                }]
            }, {
                'featureType': 'road',
                'elementType': 'labels.text.stroke',
                'stylers': [{
                    'color': '#ffffff'
                }]
            }, {
                'featureType': 'poi',
                'stylers': [{
                    'visibility': 'off'
                }]
            }, {
                'featureType': 'landscape.natural',
                'elementType': 'geometry.fill',
                'stylers': [{
                    'visibility': 'on'
                }, {
                    'color': '#b8cb93'
                }]
            }, {
                'featureType': 'poi.park',
                'stylers': [{
                    'visibility': 'on'
                }]
            }, {
                'featureType': 'poi.sports_complex',
                'stylers': [{
                    'visibility': 'on'
                }]
            }, {
                'featureType': 'poi.medical',
                'stylers': [{
                    'visibility': 'on'
                }]
            }, {
                'featureType': 'poi.business',
                'stylers': [{
                    'visibility': 'simplified'
                }]
            }]

        };
        this.map = new google.maps.Map(document.getElementById('map'), mapOptions);

        this.map.data.addGeoJson(this.data);

        this.map.data.setStyle(function (feature) {
            if (!isNullOrUndefined(feature.getProperty('info'))) {
                const active = feature.getProperty('info').active;
                const color = active ? '#90EE90' : '#FF4500';
                return {
                    fillColor: color,
                    strokeWeight: 1
                };
            }
        });

        this.addListener(this.modalService, this.bsModalRef);
    }

    addListener(modalService, bsModalRef) {
        function showInfo(allotmentUser) {
            const initialState = {
                allotmentUser: allotmentUser
            };
            bsModalRef = modalService.show(ShowInfoComponent, {initialState, class: 'modal-lg'});
        }

        this.map.data.addListener('click', function (event) {
            if (!isNullOrUndefined(event.feature.getProperty('info'))) {
                showInfo(event.feature.getProperty('info'));
            }
        });
        this.map.data.addListener('mouseover', function (event) {
            if (!isNullOrUndefined(event.feature.getProperty('info'))) {
                if (event.feature.getProperty('info')) {
                    document.getElementById('info-box').textContent =
                        event.feature.getProperty('info').allotmentDto.number;
                }
            }
        });
    }
}



