import {Component, OnInit, TemplateRef} from '@angular/core';
import {UserService} from '../services/user.service';
import {UserAllotment} from '../models/UserAllotment';
import {NotificationService} from '../services/notification.service';
import {BsModalRef, BsModalService} from 'ngx-bootstrap';
import {AllotmentInfoComponent} from './allotment-info/allotment-info.component';
import {EditUserComponent} from './edit-user/edit-user.component';
import {NgxSpinnerService} from 'ngx-spinner';
import {AddUserComponent} from './add-user/add-user.component';
import {Allotment} from '../models/Allotment';
import {User} from '../models/User';

@Component({
    selector: 'app-user',
    templateUrl: './user.component.html',
    styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

    bsModalRef: BsModalRef;
    public deactivateId: number;
    public allotmentUsers: Array<UserAllotment>;

    constructor(private userService: UserService,
                private notification: NotificationService,
                private modalService: BsModalService,
                private spinner: NgxSpinnerService) {
    }

    ngOnInit() {
        this.spinner.show();
        this.getAllotmentUserActive();
        setTimeout(() => {
            /** spinner ends after 2 seconds */
            this.spinner.hide();
        }, 1000);
    }

    getAllotmentUserActive() {
        this.userService.getAllActiveAllotmentUser().subscribe(
            (data: Array<UserAllotment>) => {
                this.allotmentUsers = data;
            },
            err => console.error(err)
        );
    }

    updateValue(value) {
        const allotmentUserDto = this.allotmentUsers.find(userAllotment => userAllotment.userDto.idUser === value.idUser);
        allotmentUserDto.userDto.name = value.name;
        allotmentUserDto.userDto.lastName = value.lastName;
        allotmentUserDto.userDto.phone = value.phone;
        allotmentUserDto.userDto.email = value.email;
        allotmentUserDto.userDto.role = 'ROLE_' + value.roleName;
        allotmentUserDto.allotmentDto.idAllotment = value.allotmentId;
        allotmentUserDto.allotmentDto.number = value.allotmentId;
    }

    addValue(value) {
        const userAllotment = new UserAllotment();
        userAllotment.active = true;
        userAllotment.allotmentDto = new Allotment(value.idAllotment, value.idAllotment, 0, 0, false, false);
        userAllotment.userDto = new User(0, value.name, value.lastName, value.phone, value.email, true, 'ROLE_' + value.roleName, value.username);
        this.allotmentUsers.push(userAllotment);
    }

    deleteValue(idUser) {
        this.allotmentUsers.forEach((item, index) => {
            if (item.userDto.idUser === idUser) {
                this.allotmentUsers.splice(index, 1);
            }
        });
    }

    addUser() {
        this.bsModalRef = this.modalService.show(AddUserComponent, {class: 'modal-lg'});
        this.bsModalRef.content.userOutput.subscribe((value) => {
            this.addValue(value);
        });
    }

    editUser(userDto, allotmentDto) {
        const initialState = {
            userEdit: userDto,
            allotmentEdit: allotmentDto
        };
        this.bsModalRef = this.modalService.show(EditUserComponent, {initialState, class: 'modal-lg'});
        this.bsModalRef.content.userOutput.subscribe((value) => {
            console.log(value);
            this.updateValue(value);
        });
    }

    deactivateUser(template: TemplateRef<any>, id) {
        this.deactivateId = id;
        this.bsModalRef = this.modalService.show(template, {class: 'modal-lg'});
    }

    confirm(): void {
        this.userService.deactivateUser(this.deactivateId).subscribe(
            (response) => {
                this.notification.showNotification('top', 'center', 'success', response);
            },
            err => console.error(err)
        );
        this.deleteValue(this.deactivateId);
        this.bsModalRef.hide();
    }

    decline(): void {
        this.bsModalRef.hide();
    }


    allotmentInfo(allotmentDto) {
        const initialState = {
            allotmentInfo: allotmentDto
        };
        this.bsModalRef = this.modalService.show(AllotmentInfoComponent, {initialState, class: 'modal-lg'});
    }
}



