import {Component, OnInit} from '@angular/core';
import {MailBody} from '../../models/MailBody';
import {MailService} from '../../services/mail.service';
import {UserAllotment} from '../../models/UserAllotment';
import {UserService} from '../../services/user.service';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap';
import {NotificationService} from '../../services/notification.service';

@Component({
    selector: 'app-add-mail',
    templateUrl: './add-mail.component.html'
})
export class AddMailComponent implements OnInit {

    public mailForm: FormGroup;
    public allotmentUsers: Array<UserAllotment>;
    items = new FormControl();
    itemList = ['item 1', 'item 2', 'item 3', 'item 4', 'item 5', 'item 6'];

    constructor(public bsModalRef: BsModalRef,
                private mailService: MailService,
                private userService: UserService,
                private notificationService: NotificationService) {
    }

    ngOnInit() {
        this.getAllotmentUserActive();
        this.mailForm = new FormGroup({
            'subject': new FormControl('', [
                Validators.required,
                Validators.minLength(1),
                Validators.maxLength(255)
            ]),
            'text': new FormControl('', [
                Validators.required,
                Validators.minLength(1),
                Validators.maxLength(100000)
            ]),
            'receivers': new FormControl([])
        });
    }

    getAllotmentUserActive() {
        this.userService.getAllActiveAllotmentUser().subscribe(
            (data: Array<UserAllotment>) => {
                this.allotmentUsers = data;
            },
            err => console.error(err)
        );
    }

    close() {
        this.bsModalRef.hide();
    }

    submit() {
        this.mailService.newMail(this.mailForm.getRawValue()).subscribe
        (response => {
                this.bsModalRef.hide();
                this.notificationService.showNotification('top', 'center', 'success', response);
            },
            err => {
                console.log(err);
            });
    }
}



