import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {BsModalRef} from 'ngx-bootstrap';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {NotificationService} from '../../services/notification.service';
import {PaymentService} from '../../services/payment.service';
import {UserAllotment} from '../../models/UserAllotment';
import {UserService} from '../../services/user.service';
import {Payment} from '../../models/Payment';

@Component({
    selector: 'app-add-payment',
    templateUrl: './add-payment.component.html',
    styleUrls:['./add-payment.component.css']
})

export class AddPaymentComponent implements OnInit {

    public paymentForm: FormGroup;
    private payment: Payment;
    public allotmentUsers: Array<UserAllotment>;
    @Output() paymentOutput = new EventEmitter();

    constructor(public bsModalRef: BsModalRef,
                private userService: UserService,
                private paymentService: PaymentService,
                private notificationService: NotificationService) {
    }

    ngOnInit() {
        this.getAllotmentUserActive();
        this.paymentForm = new FormGroup({
            'charge': new FormControl(0, [
                Validators.required,
                Validators.min(1),
                Validators.max(1000)
            ]),
            'title': new FormControl('Title', [
                Validators.required,
                Validators.minLength(1),
                Validators.maxLength(255)
            ]),
            'paid': new FormControl(false, [
                Validators.required
            ]),
            'userId': new FormControl(0, [
                Validators.required,
                Validators.min(1)
            ])
        });
    }

    getAllotmentUserActive() {
        this.userService.getAllActiveAllotmentUser().subscribe(
            (data: Array<UserAllotment>) => {
                this.allotmentUsers = data;
            },
            err => console.error(err)
        );
    }

    close() {
        this.bsModalRef.hide();
    }

    createPayment() {
        const allotmentUser = this.allotmentUsers.find(item => item.userDto.idUser === this.paymentForm.value.userId);
        this.payment = new Payment();
        this.payment.userDto = allotmentUser.userDto;
        this.payment.allotmentDto = allotmentUser.allotmentDto;
        this.payment.charge = this.paymentForm.value.charge;
        this.payment.title = this.paymentForm.value.title;
        return this.payment;
    }

    submit() {
        this.paymentService.newPayment(this.paymentForm.getRawValue())
            .subscribe(response => {
                    this.bsModalRef.hide();
                    this.notificationService.showNotification('top', 'center', 'success', response);
                    this.paymentOutput.emit(this.createPayment());

                },
                err => {
                    console.log(err);
                });
    }
}



