import {Component, OnInit} from '@angular/core';
import {NotificationService} from '../services/notification.service';
import {BsModalRef, BsModalService} from 'ngx-bootstrap';
import {Allotment} from '../models/Allotment';
import {AllotmentService} from '../services/allotment.service';
import {HistoryAllotmentComponent} from './historyAllotment/history-allotment.component';
import {EditAllotmentComponent} from './editAllotment/edit-allotment.component';
import {NgxSpinnerService} from 'ngx-spinner';
import {UserAllotment} from "../models/UserAllotment";

@Component({
    selector: 'app-allotment',
    templateUrl: './allotment.component.html'
})
export class AllotmentComponent implements OnInit {

    bsModalRef: BsModalRef;
    public allotmentList: Array<UserAllotment>;

    constructor(private allotmentService: AllotmentService,
                private notification: NotificationService,
                private spinner: NgxSpinnerService,
                private modalService: BsModalService) {
    }

    ngOnInit() {
        this.spinner.show();
        this.getAllAllotment();
        setTimeout(() => {
            /** spinner ends after 2 seconds */
            this.spinner.hide();
        }, 2000);
    }

    getAllAllotment() {
        this.allotmentService.getAllAllotments().subscribe(
            (data: Array<UserAllotment>) => {
                this.allotmentList = data;
            },
            err => console.error(err)
        );
    }

    historyAllotment(idAllotment) {
        const initialState = {
            idAllotment: idAllotment,
        };

        this.bsModalRef = this.modalService.show(HistoryAllotmentComponent, {initialState, class: 'modal-lg'});
        this.bsModalRef.content.closeBtnName = 'Close';
    }

    updateValue(value) {
        this.allotmentList.find(userAllotment => userAllotment.allotmentDto.idAllotment === value.idAllotment).allotmentDto = value;
    }

    editAllotment(allotmentDto) {
        const initialState = {
            allotmentEdit: allotmentDto
        };
        this.bsModalRef = this.modalService.show(EditAllotmentComponent, {initialState, class: 'modal-lg'});
        this.bsModalRef.content.allotmentOutput.subscribe((value) => {
            this.updateValue(value);
        });
    }

}



