export class User {
    idUser: number;
    name: string;
    lastName: string;
    phone: string;
    email: string;
    active: boolean;
    role: string;
    username: string;

    constructor(idUser: number,
                name: string,
                lastName: string,
                phone: string,
                email: string,
                active: boolean,
                role: string,
                username: string) {
        this.idUser = idUser;
        this.name = name;
        this.lastName = lastName;
        this.username = username;
        this.phone = phone;
        this.email = email;
        this.active = active;
        this.role = role;
    }
}




