import {Component, Input, OnInit} from '@angular/core';
import {BsModalRef} from 'ngx-bootstrap';
import {AllotmentService} from '../../services/allotment.service';
import {HistoryAllotment} from '../../models/HistoryAllotment';

@Component({
    selector: 'app-history-allotment',
    templateUrl: './history-allotment.component.html'
})
export class HistoryAllotmentComponent implements OnInit {

    idAllotment;
    public loaded = false;
    public allotmentHistory: HistoryAllotment;

    constructor(public bsModalRef: BsModalRef, private allotmentService: AllotmentService) {
    }

    ngOnInit() {
        this.getHistoryAllotment();
    }

    getHistoryAllotment() {
        this.allotmentService.getAllAllotmentHistory(this.idAllotment).subscribe(
            (data: HistoryAllotment) => {
                this.loaded = true;
                this.allotmentHistory = data;
            }
        );
    }
}



