import { Routes } from '@angular/router';

import { DashboardComponent } from '../../dashboard/dashboard.component';
import { UserProfileComponent } from '../../user-profile/user-profile.component';
import { PaymentComponent } from '../../payments/payment.component';
import { ArticlesComponent } from '../../articles/articles.component';
import { MapsComponent } from '../../maps/maps.component';
import { UpgradeComponent } from '../../upgrade/upgrade.component';
import {AuthGuard} from '../../auth/auth-guard.service';
import {UserComponent} from '../../users/user.component';
import {AllotmentComponent} from '../../allotment/allotment.component';
import {SigninComponent} from '../../auth/signin/signin.component';
import {MailComponent} from '../../mails/mail.component';
import {AllotmentProfileComponent} from '../../allotment/allotmentProfile/allotment-profile.component';

export const AdminLayoutRoutes: Routes = [
    // {
    //   path: '',
    //   children: [ {
    //     path: 'dashboard',
    //     component: DashboardComponent
    // }]}, {
    // path: '',
    // children: [ {
    //   path: 'userprofile',
    //   component: UserProfileComponent
    // }]
    // }, {
    //   path: '',
    //   children: [ {
    //     path: 'icons',
    //     component: IconsComponent
    //     }]
    // }, {
    //     path: '',
    //     children: [ {
    //         path: 'notifications',
    //         component: NotificationsComponent
    //     }]
    // }, {
    //     path: '',
    //     children: [ {
    //         path: 'maps',
    //         component: MapsComponent
    //     }]
    // }, {
    //     path: '',
    //     children: [ {
    //         path: 'typography',
    //         component: TypographyComponent
    //     }]
    // }, {
    //     path: '',
    //     children: [ {
    //         path: 'upgrade',
    //         component: UpgradeComponent
    //     }]
    // }
    {path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard]},
    {path: 'user-profile', component: UserProfileComponent, canActivate: [AuthGuard]},
    {path: 'users', component: UserComponent, canActivate: [AuthGuard]},
    {path: 'allotment', component: AllotmentProfileComponent, canActivate: [AuthGuard]},
    {path: 'allotments', component: AllotmentComponent, canActivate: [AuthGuard]},
    {path: 'payments', component: PaymentComponent, canActivate: [AuthGuard]},
    {path: 'articles', component: ArticlesComponent, canActivate: [AuthGuard]},
    {path: 'maps', component: MapsComponent, canActivate: [AuthGuard]},
    {path: 'mails', component: MailComponent, canActivate: [AuthGuard]},
    {path: 'upgrade', component: UpgradeComponent, canActivate: [AuthGuard]},
    {path: 'login', component: SigninComponent},
    {path: '', redirectTo: 'login', pathMatch: 'full'}
];



