import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {AdminLayoutRoutes} from './admin-layout.routing';
import {DashboardComponent} from '../../dashboard/dashboard.component';
import {UserProfileComponent} from '../../user-profile/user-profile.component';
import {PaymentComponent} from '../../payments/payment.component';
import {ArticlesComponent} from '../../articles/articles.component';
import {MapsComponent} from '../../maps/maps.component';
import {MailComponent} from '../../mails/mail.component';
import {UpgradeComponent} from '../../upgrade/upgrade.component';
import {SigninComponent} from '../../auth/signin/signin.component';
import {EditUserComponent} from '../../users/edit-user/edit-user.component';
import {AllotmentComponent} from '../../allotment/allotment.component';
import {HistoryAllotmentComponent} from '../../allotment/historyAllotment/history-allotment.component';
import {AllotmentInfoComponent} from '../../users/allotment-info/allotment-info.component';
import {EditAllotmentComponent} from '../../allotment/editAllotment/edit-allotment.component';

import {
    MatButtonModule,
    MatSelectModule,
    MatInputModule,
    MatRippleModule,
    MatTooltipModule,
    MatSort,
    MatTableDataSource
} from '@angular/material';
import {AlertModule, ModalModule} from 'ngx-bootstrap';
import {UserComponent} from 'app/users/user.component';
import {NgxSpinnerModule} from 'ngx-spinner';
import {AddUserComponent} from '../../users/add-user/add-user.component';
import {EditPaymentComponent} from '../../payments/edit-payment/edit-payment.component';
import {AddPaymentComponent} from '../../payments/add-payment/add-payment.component';
import {EditArticleComponent} from '../../articles/edit-article/edit-article.component';
import {AddArticleComponent} from '../../articles/add-article/add-article.component';
import {AddCommentaryComponent} from '../../articles/commentary/add-commentary/add-commentary.component';
import {EditCommentaryComponent} from '../../articles/commentary/edit-commentary/edit-commentary.component';
import {AddMailComponent} from '../../mails/add-mail/add-mail.component';
import {AllotmentProfileComponent} from '../../allotment/allotmentProfile/allotment-profile.component';
import {ShowInfoComponent} from '../../maps/show-info/show-info.component';


@NgModule({
    imports: [
        CommonModule,
        RouterModule.forChild(AdminLayoutRoutes),
        FormsModule,
        ReactiveFormsModule,
        MatButtonModule,
        MatRippleModule,
        MatInputModule,
        MatSelectModule,
        MatTooltipModule,
        NgxSpinnerModule,
        ModalModule.forRoot(),
        AlertModule.forRoot()
    ],
    declarations: [
        DashboardComponent,
        UserProfileComponent,
        PaymentComponent,
        ArticlesComponent,
        ShowInfoComponent,
        AllotmentProfileComponent,
        AddArticleComponent,
        EditArticleComponent,
        AddCommentaryComponent,
        EditCommentaryComponent,
        MapsComponent,
        MailComponent,
        AddMailComponent,
        UpgradeComponent,
        SigninComponent,
        UserComponent,
        AddUserComponent,
        EditUserComponent,
        AddPaymentComponent,
        EditPaymentComponent,
        AllotmentComponent,
        HistoryAllotmentComponent,
        AllotmentInfoComponent,
        EditAllotmentComponent
    ],
    entryComponents: [
        ShowInfoComponent,
        AddMailComponent,
        AddUserComponent,
        EditUserComponent,
        AddArticleComponent,
        EditArticleComponent,
        AddCommentaryComponent,
        EditCommentaryComponent,
        HistoryAllotmentComponent,
        AllotmentInfoComponent,
        EditAllotmentComponent,
        AddPaymentComponent,
        EditPaymentComponent]
})

export class AdminLayoutModule {
}



