import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {BsModalRef} from 'ngx-bootstrap';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {CommentaryService} from '../../../services/commentary.service';
import {NotificationService} from '../../../services/notification.service';

@Component({
    selector: 'app-add-commentary',
    templateUrl: './add-commentary.component.html'
})
export class AddCommentaryComponent implements OnInit {

    public commentaryForm: FormGroup;
    @Input() article;
    @Output() commentaryOutput = new EventEmitter();

    constructor(public bsModalRef: BsModalRef,
                private commentaryService: CommentaryService,
                private notificationService: NotificationService) {
    }

    ngOnInit() {
        this.commentaryForm = new FormGroup({
            'idArticle': new FormControl({value: this.article.idArticle, disabled: true}),
            'text': new FormControl('', [
                Validators.required,
                Validators.minLength(1),
                Validators.maxLength(500)
            ])
        });
    }

    close() {
        this.bsModalRef.hide();
    }

    submit() {
        this.commentaryService.newCommentary(this.commentaryForm.getRawValue())
            .subscribe(response => {
                    this.bsModalRef.hide();
                    this.notificationService.showNotification('top', 'center', 'success', response);
                    this.commentaryOutput.emit(this.commentaryForm.value);
                },
                err => {
                    this.bsModalRef.hide();
                    console.log(err);
                    this.notificationService.showNotification('top', 'center', 'danger', JSON.parse(err.error)['message']);
                });
    }
}



