import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {BsModalRef} from 'ngx-bootstrap';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {NotificationService} from '../../services/notification.service';
import {PaymentService} from '../../services/payment.service';

@Component({
    selector: 'app-edit-payment',
    templateUrl: './edit-payment.component.html'
})

export class EditPaymentComponent implements OnInit {

    public paymentForm: FormGroup;
    @Input() paymentEdit;
    @Output() paymentOutput = new EventEmitter();


    constructor(public bsModalRef: BsModalRef,
                private paymentService: PaymentService,
                private notificationService: NotificationService) {
    }

    ngOnInit() {
        this.paymentForm = new FormGroup({
            'idPayment': new FormControl({value: this.paymentEdit.idPayment, disabled: true}, Validators.required),
            'charge': new FormControl(this.paymentEdit.charge, [
                Validators.required,
                Validators.min(1),
                Validators.max(1000)
            ]),
            'title': new FormControl(this.paymentEdit.title, [
                Validators.required,
                Validators.minLength(1),
                Validators.maxLength(255)
            ]),
            'paid': new FormControl(this.paymentEdit.paid, [
                Validators.required
            ]),
            'userId': new FormControl({value: this.paymentEdit.userDto.idUser, disabled: true}, Validators.required)
        });
    }

    close() {
        this.bsModalRef.hide();
    }

    submit() {
        this.paymentService.updatePayment(this.paymentForm.getRawValue())
            .subscribe(response => {
                    this.bsModalRef.hide();
                    this.notificationService.showNotification('top', 'center', 'success', response);
                    this.paymentOutput.emit(this.paymentForm.getRawValue());
                },
                err => {
                    console.log(err);
                });
    }
}



