export class Allotment {
    idAllotment: number;
    number: number;
    squaremeter: number;
    treeNumber: number;
    bower: boolean;
    composter: boolean;

    constructor(idAllotment: number, number: number, squaremeter: number, treeNumber: number, bower: boolean, composter: boolean) {
        this.idAllotment = idAllotment;
        this.number = number;
        this.squaremeter = squaremeter;
        this.treeNumber = treeNumber;
        this.bower = bower;
        this.composter = composter;
    }
}




