import {Component, HostListener, OnInit, TemplateRef} from '@angular/core';
import {PaymentService} from '../services/payment.service';
import {Payment} from '../models/Payment';
import {NgxSpinnerService} from 'ngx-spinner';
import {BsModalRef, BsModalService} from 'ngx-bootstrap';
import {NotificationService} from '../services/notification.service';
import {AuthenticationService} from '../auth/authentication.service';
import {EditPaymentComponent} from './edit-payment/edit-payment.component';
import {AddPaymentComponent} from './add-payment/add-payment.component';
import {environment} from 'environments/environment';
import * as FileSaver from 'file-saver';

@Component({
    selector: 'app-payment',
    templateUrl: './payment.component.html',
    styleUrls: ['./payment.component.css']
})
export class PaymentComponent implements OnInit {

    bsModalRef: BsModalRef;
    handler: any;
    public text: string;
    public idPayment: number;
    public role: string;
    public payments: Array<Payment>;

    constructor(private paymentService: PaymentService,
                private notification: NotificationService,
                private modalService: BsModalService,
                private spinner: NgxSpinnerService,
                private authenticationService: AuthenticationService) {
    }

    ngOnInit() {
        this.role = this.authenticationService.getRole();
        this.spinner.show();
        if (this.role === 'ADMIN') {
            this.getAllPayments();
        } else {
            this.getUserPayments();
            this.handler = StripeCheckout.configure({
                key: environment.stripeKey,
                image: '../assets/img/pzd.png',
                locale: 'en',
                token: token => {
                    this.paymentService.confrimPayment(this.idPayment).subscribe(
                        (response) => {
                            this.notification.showNotification('top', 'center', 'success', response);
                            this.payments.find(payments => payments.idPayment === this.idPayment).paid = true;
                        },
                        err => console.error(err)
                    );
                }
            });
        }
        setTimeout(() => {
            /** spinner ends after 2 seconds */
            this.spinner.hide();
        }, 1000);
    }

    getAllPayments() {
        this.paymentService.getAllPayments().subscribe(
            (data: Array<Payment>) => {
                this.payments = data;
            },
            err => console.error(err)
        );
    }

    getUserPayments() {
        this.paymentService.getPaymentsForUser().subscribe(
            (data: Array<Payment>) => {
                this.payments = data;
            },
            err => console.error(err)
        );
    }

    pay(payment) {
        this.idPayment = payment.idPayment;
        this.handler.open({
            name: 'ROD Obroca',
            description: payment.title,
            locale: 'en',
            currency: 'PLN',
            amount: payment.charge * 100
        })
    }

    @HostListener('window:popstate')
    onPopstate() {
        this.handler.close()
    }

    addPayment() {
        this.bsModalRef = this.modalService.show(AddPaymentComponent, {class: 'modal-lg'});
        this.bsModalRef.content.paymentOutput.subscribe((value) => {
            this.addValue(value);
        });
    }

    addValue(value) {
        const paymentDto = new Payment();
        paymentDto.charge = value.charge;
        paymentDto.title = value.title;
        paymentDto.userDto = value.userDto;
        paymentDto.allotmentDto = value.allotmentDto;

        this.payments.push(paymentDto);
    }

    confirmPayment(template: TemplateRef<any>, id, text) {
        this.text = text;
        this.idPayment = id;
        this.bsModalRef = this.modalService.show(template, {class: 'modal-lg'});
    }

    editPayment(payment) {
        const initialState = {
            paymentEdit: payment
        };
        this.bsModalRef = this.modalService.show(EditPaymentComponent, {initialState, class: 'modal-lg'});
        this.bsModalRef.content.paymentOutput.subscribe((value) => {
            this.updateValue(value);
        });
    }

    updateValue(value) {
        const paymentDto = this.payments.find(payment => payment.idPayment === value.idPayment);
        paymentDto.charge = value.charge;
        paymentDto.title = value.title;
    }

    deletePayment(template: TemplateRef<any>, id, text) {
        this.text = text;
        this.idPayment = id;
        this.bsModalRef = this.modalService.show(template, {class: 'modal-lg'});
    }


    confirm(): void {
        if (this.text === 'delete') {
            this.paymentService.deletePayment(this.idPayment).subscribe(
                (response) => {
                    this.notification.showNotification('top', 'center', 'success', response);
                },
                err => console.error(err)
            );
            this.payments.forEach((item, index) => {
                if (item.idPayment === this.idPayment) {
                    this.payments.splice(index, 1);
                }
            });
            this.bsModalRef.hide();
        } else if (this.text === 'decline') {
            this.paymentService.declinePayment(this.idPayment).subscribe(
                (response) => {
                    this.notification.showNotification('top', 'center', 'success', response);
                },
                err => console.error(err)
            );
            this.payments.find(payments => payments.idPayment === this.idPayment).paid = false;
            this.bsModalRef.hide();
        } else {
            this.paymentService.confrimPayment(this.idPayment).subscribe(
                (response) => {
                    this.notification.showNotification('top', 'center', 'success', response);
                },
                err => console.error(err)
            );
            this.payments.find(payments => payments.idPayment === this.idPayment).paid = true;
            this.bsModalRef.hide();
        }
    }

    decline(): void {
        this.bsModalRef.hide();
    }

    generateRecip(payment) {
        this.paymentService.generateRecip(payment.idPayment).subscribe(
            (response) => {
                // const blob = new Blob(response), {type: 'application/pdf'});
                const filename = 'payment_' + new Date() + '.pdf';
                FileSaver.saveAs(response, filename);
            },
            err => console.error(err)
        );
    }

}



