import {Component, OnInit} from '@angular/core';
import {NgxSpinnerService} from 'ngx-spinner';
import {UserAllotment} from '../../models/UserAllotment';
import {NotificationService} from 'app/services/notification.service';
import {AllotmentService} from '../../services/allotment.service';
import {Allotment} from '../../models/Allotment';
import {FormControl, FormGroup, Validators} from '@angular/forms';

@Component({
    selector: 'app-allotment-profile',
    templateUrl: './allotment-profile.component.html'
})
export class AllotmentProfileComponent implements OnInit {

    public loaded = false;
    public allotmentEdit: Allotment;
    public allotmentForm: FormGroup;

    constructor(private allotmentService: AllotmentService) {
    }

    ngOnInit() {
        this.getAllotment();
    }

    getAllotment() {
        this.allotmentService.getAllotmentForUser().subscribe(
            (data: Allotment) => {
                this.allotmentEdit = data;
                this.initForm();
                this.loaded = true;
            },
            (err) => {
                console.error(err);
            });
    }

    initForm() {
        this.allotmentForm = new FormGroup({
            'idAllotment': new FormControl({value: this.allotmentEdit.idAllotment, disabled: true}),
            'number': new FormControl({value: this.allotmentEdit.number, disabled: true}),
            'squaremeter': new FormControl({value: this.allotmentEdit.squaremeter, disabled: true}),
            'treeNumber': new FormControl({value: this.allotmentEdit.treeNumber, disabled: true}),
            'composter': new FormControl({value: this.allotmentEdit.composter, disabled: true}),
            'bower': new FormControl({value: this.allotmentEdit.bower, disabled: true})
        });
    }

}



