import {Component, OnInit} from '@angular/core';
import {MailBody} from '../models/MailBody';
import {MailService} from '../services/mail.service';
import {AddMailComponent} from './add-mail/add-mail.component';
import {BsModalRef, BsModalService} from 'ngx-bootstrap';

@Component({
    selector: 'app-mails',
    templateUrl: './mail.component.html',
    styleUrls: ['./mail.component.css']
})
export class MailComponent implements OnInit {

    public loaded = false;
    bsModalRef: BsModalRef;
    public mails: Array<MailBody>;

    constructor(private mailService: MailService, private modalService: BsModalService) {
    }

    ngOnInit() {
        this.getAllMails()
    }

    getAllMails() {
        this.mailService.getAllMails().subscribe(
            (data: Array<MailBody>) => {
                this.mails = data;
                this.loaded = true;
            },
            err => console.error(err)
        );
    }

    newMail() {
        this.bsModalRef = this.modalService.show(AddMailComponent, { class: 'modal-lg'});
    }

    seen(mail) {
        if (!mail.seen) {
            this.mailService.seen(mail.idMail).subscribe(
                () => {
                    this.mails.find(item => item.idMail === mail.idMail).seen = true;
                },
                err => console.error(err)
            );
        }
    }

}



